/* ------------------------------------------- */
/*              SEARCH RESULTS                 */
/* ------------------------------------------- */

.tp-search-res {

    &__info {
        color: var(--c-blue-dark);
        font-weight: 300;
        font-size: 24px;
        font-family: $f-oswald;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        
        @include medium-size-min {
            margin-top: -10px;
        }

        span {
            color: $color-main;
            font-weight: 500;
        }

        > div:first-child {
            margin-right: 20px;
        }
    }

    &__info-checkbox {
        text-transform: uppercase;
        position: relative;
        z-index: 1;
        padding: 8px 18px;
        width: 120px;
        height: 40px;
        display: flex;
        align-items: center;
        margin-top: 10px;
        margin-bottom: 10px;

        @include small-size-max {
            margin-left: 0;
            margin-top: 20px;
            width: 100%;
            margin-bottom: 10px;
        }

        &:before {
            @include skew(var(--c-jade));
        }

        input[type="checkbox"] + label {
            font-size: 16px;
            font-weight: 500;
            color: var(--c-white);
            font-family: $f-oswald;
            padding-left: 25px;
            line-height: 1;
            width: 100%;

            &:after {
                left: 3px;
                top: 50%;
                transform: translateY(-50%);
            }

            &:before {
                border-color: var(--c-white);
                width: 15px;
                height: 15px;
                top: 50%;
                transform: translateY(-50%);
            }

            &:hover {
                
                &:after {
                    @include mobile-menu-min {
                        color: var(--c-white);
                        opacity: 0.7;
                    }
                }
            }
        }

        input[type="checkbox"]:checked + label {
            
            &:before {
                background-color: var(--c-white);
            }

            &:after {
                color: var(--c-jade);
            }
        }
    }

    &__fltr {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -3px;
    }

    &__fltr-item {
        position: relative;
        color: $color-main;
        font-weight: 700;
        @include flex-center-vert;
        margin: 6px;
        padding: 8.5px 13px;
        z-index: 0;
        font-size: 15px;
        height: 49px;
        min-height: 49px;

        @include small-size-max {
            margin: 4px 4px;
            padding: 5px 9px;
            font-size: 14px;
            height: 34px;
            min-height: 34px;
        }

        &:last-child {
            margin-bottom: 15px;
        
            @include small-size-max {
                margin-bottom: 25px;
            }
        }

        &:before {
            @include skew($color-main);
            opacity: 0.07;
            z-index: -1;
        }

        i {
            position: relative;
            top: 1.5px;
            font-style: 14px;
            font-weight: normal;
            cursor: pointer;
            margin-left: 8px;
            transition: 250ms ease-in;

            &:hover {
                color: $color-alter;
            }
        }
    }

    &__box {
        .tp-product__image,
        .tp-swiper--product .tp-swiper__slide {
            
            @media(max-width: 991px) and (min-width: 768px) {
                height: 200px;
            }
        }
    }
}