/* ------------------------------------------- */
/*               FOOTER CONTACT                */
/* ------------------------------------------- */

.tp-contact {

    @include medium-size-max {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__item {
        display: flex;
        margin-bottom: 25px;

        @include small-size-max {
            margin-bottom: 10px;
        }

        &:last-child {
            margin-bottom: 0;
        }

        .fa {
            position: relative;
            top: 2px;
            color: var(--c-white);
            font-size: 22px;
            margin-right: 10px;
        }

        a {
            color: inherit;

            &:hover {
                color: $color-alter;
            }
        }
    }
}