/* ------------------------------------------- */
/*             	   PARTNERS	           		   */
/* ------------------------------------------- */

.tp-partners {
	padding: 40px 0;
	
	&--border{
		border-top: 1px solid var(--c-grey-light);
	}

    &__box {
		@include flex-center-vert;
        flex-wrap: wrap;
	}

	&__item {
		margin: 10px 0px;
        padding: 0 10px;
        
		img,
		picture {
			width: auto;
			height: auto;
			max-height: 80px;
		}
	}
}
