/* ------------------------------------------- */
/*                  LOGO                       */
/* ------------------------------------------- */

.tp-logo {

    img {
        min-width: 248px;

        @include extra-large-size-max {
            min-width: 220px;
            width: 220px;
        }

        @include large-size-max {
            min-width: 190px;
            width: 190px;
            height: 52px;
        }

        @include mobile-menu-max {
            min-width: 180px;
            width: auto;
            height: 50px;
        }

        @media(max-width: 370px) {
            min-width: 120px;
            width: 140px;
        }
    }

    &--laptop {
        margin-right: auto;
        margin-top: 15px;

        @include extra-large-size-min {
            display: none;
        }

        @include mobile-menu-max {
            display: none;
        }
    }

    &--mobile {
        margin: 10px 0;

        @include mobile-menu-min {
            display: none;
        }
    }
    
}