// FONTS
$f-roboto: 'Roboto Slab', serif;
$f-oswald: 'Oswald', sans-serif;

:root {
	--c-white: #fff;
	--ca-white: 255, 255, 255;
	--c-white-grey: #f0f2f6;
	--ca-white-grey: 240, 242, 246;

	--c-black: #000;
	--c-black-2: #222;
	--ca-black: 0, 0, 0;

	--c-grey-light: #eee;
	--ca-grey-light: 238, 238, 238;
	--c-grey-medium: #d8d8d8;
	--ca-grey-medium: 216, 216, 216;
	--c-grey: #6b7179;
	--ca-grey: 107, 113, 121;
	--c-athens-grey: #e9ebee;
	--c-porcelain: #f4f5f6;
	--c-athens: #e2e4e8;
	--c-gray-dark: #e1e5ed;

	--c-blue-main: #264d80;
	--ca-blue-main: 38, 77, 128;
	--c-blue-light: #11396c;
	--ca-blue-light: 17, 57, 108;
	--c-blue-grey: #2d3f59;
	--ca-blue-grey: 45, 63, 89;
	--c-blue-medium: #0c284d;
	--ca-blue-medium: 12, 40, 77;
	--c-blue-dark: #142133;
	--ca-blue-dark: 20, 33, 51;
	--c-blue-darken: #0f1926;
	--ca-blue-darken: 15, 25, 38;
	--c-blue-facebook: #3b5998;
	--ca-blue-facebook: 59, 89, 152;

	--c-jade: #00b67a;
	--ca-jade: 0, 182, 122;
	--c-emerald: #4cbf74;
	--c-lima: #73cf11;
	--c-supernova: #ffce00;
	--c-tango: #f37324;
	--c-crimson: #e12729;
	--c-pomegranate: #f3383f;
	--ca-pomegranate: 243, 56, 63;
	--c-sunset: #ff4d53;
	--c-solitude: #f1f3f7;

	--windowHeight: 100vh;
	--productImageH: 200px;
	--productImageH-md: 240px;
	--prodGalleryThumbWidth: 115px;
}

$container-max-widths: (
	sm: 540px,
	md: 720px,
	lg: 960px,
	xl: 1200px
);

// MOBILE MENU BREAKPOINTS
$bp-mob-menu: 1025px;

// Z-INDEX
$z-index-header: 900;


//
//	BOOTSTRAP
//

// GRID
$enable-grid-classes: true !default;

// Grid breakpoints
$grid-breakpoints: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$grid-breakpoints: map-merge(
	(
		xs: 0,
		sm: 576px,
		md: 768px,
		lg: 992px,
		xl: 1200px,
		xxl: 1600px,
	),
	$grid-breakpoints
);

@include _assert-ascending($grid-breakpoints, '$grid-breakpoints');
@include _assert-starts-at-zero($grid-breakpoints);

// Grid containers
$container-max-widths: () !default;
$container-max-widths: map-merge(
	(
		sm: 540px,
		md: 720px,
		lg: 960px,
		xl: 1140px,
	),
	$container-max-widths
);

@include _assert-ascending($container-max-widths, '$container-max-widths');

// Grid columns
$grid-columns: 12 !default;
$grid-gutter-width: 30px !default;


// Gradients
$grad-1: linear-gradient(to top, rgba(20, 33, 51, 0.6) 0%, rgba(20, 33, 51, 0) 100%);