/* ------------------------------------------- */
/*                  SKEW BG                    */
/* ------------------------------------------- */

.tp-skew {
    $b: &;
    position: relative;
    z-index: 1;
        
    &:before {
        @include skew($color-alter);
    }

    &--border {

        &:before {
            border: 2px solid var(--c-white);
        }
    }

    &--shadow {
        
        &:before {
            box-shadow: 4px 4px 0 var(--c-white);
        }
    }

    &--shadow-grey {
        
        &:before {
            box-shadow: 10px 10px 0 rgba(var(--ca-blue-grey), 0.15);
        }
    }

    &--blue {

        &:before {
            background-color: var(--c-blue-medium);
        }

        &#{$b}--header {
            
            @include mobile-menu-max {
                left: -20px;
            }
        }
    }

    &--blue-d {
      
        &:before {
            background-color: var(--c-blue-dark);
        }

        &#{$b}--header {
            
            @include mobile-menu-max {
                left: -20px;
            }
        }
    }

    &--blue-l {
      
        &:before {
            background-color: var(--c-blue-light);
        }
    }

    &--absolute {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: -1;
    }

    &--header {
        &:before {

            @include mobile-menu-max {
                width: calc(100% + 20px);
            }
        } 
    }
}

.tp-shadow-grey {
    box-shadow: 10px 10px 0 rgba(var(--ca-blue-grey), 0.15);
}