.tp-contact-us {
    @include extra-small-size-max {
        text-align: center;
    }

    p {
        color: var(--c-blue-dark);
        font-size: 18px;
        font-weight: 400;
        line-height: 28px;
    }

    .h4 b a {
        color: #fff;

        &:hover {
            opacity: 0.9;
        }
    }

    address {
        b {
            margin-bottom: 10px;
            display: block;
            color: var(--c-blue-dark);
            font-style: normal;
        }

        span {
            color: var(--c-grey);
            font-size: 15px;
            line-height: 26px;
            font-style: normal;
        }
    }

    &__social {
        &-wrapper {
            display: flex;
            align-items: center;
            margin-bottom: 20px;

            &:last-child {
                margin-bottom: 0;

                @include small-size-max {
                    margin-bottom: 30px;
                }
            }

            @include extra-small-size-max {
                justify-content: center;
            }

            @media screen and (max-width: 450px) {
                flex-direction: column;
            }

            a {
                color: var(--c-grey);
                font-size: 18px;
                line-height: 28px;

                &:hover {
                    color: var(--c-blue-main);
                }
            }
        }

        &-icon {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 20px;

            @media screen and (max-width: 450px) {
                margin-right: 0;
                margin-bottom: 20px;
            }

            &:hover {
                opacity: 0.9;
            }
            
            &--fb {
                background-color: var(--c-blue-facebook);
            }

            &--ig {
                background-color: var(--c-pomegranate);
            }

            i {
                font-size: 24px;
                color: #fff;
            }
        }
    }
}