/* ------------------------------------------- */
/*                SEARCH FILTER                */
/* ------------------------------------------- */

.tp-s-filter {
    $b: &;

    position: absolute;
    width: calc(100% - 30px);
    left: 15px;
    bottom: -25px;
    padding: 2px;
    z-index: 1;

    @include mobile-menu-max {
        position: relative;
        left: 0;
        bottom: auto;
        width: 100%;
        max-width: 540px;
        margin: 0 auto;
    }

    &:before {
        
        @include mobile-menu-max {
            display: none;
        }
    }

    &--vert {
        @include extra-small-size-max {
            max-width: 95%;
        }
    }

    .select2-selection__rendered {
        line-height: 1.1 !important;
        white-space: pre-wrap !important;
        padding-right: 35px !important;
    }

    &--vert {
        width: 100%;
        bottom: auto;
        left: 0;
        top: auto;
        padding: 35px 30px;
        bottom: -45px;
        margin: 0;

        @include mobile-menu-max {
            bottom: auto;
            margin: 0 auto;
            padding: 0;
            margin-bottom: -10px;
        }

        &:before {
            transform: none;
        }

        span.select2-container--default .select2-selection--single,
        .select2-container--default .select2-selection--single .select2-selection__arrow {
            height: 66px;
        }

        span.select2 {
            width: 100%!important;
        }

        #{$b} {

            &__form {
                flex-wrap: wrap;
            }

            &__options {
                flex-wrap: wrap;
                padding: 0;
            }

            &__item {
                width: 100%;
                max-width: 100%;
                flex-basis: 100%;

                &:first-child {
                    margin-bottom: 20px;
                }
            }
        }

        .tp-btn {
            padding: 19px 30px;

            &:before {
                box-shadow: 2px 2px 0 var(--c-white);
            }
        }
    }

    &__stat {
        color: var(--c-white);
        font-size: 20px;
        font-family: $f-oswald;

        span {
            font-weight: 500;
            font-size: 24px;
            margin-right: 6px;
        }
    }
    
    &__form {
        display: flex;

        @include mobile-menu-max {
            flex-wrap: wrap;
        }
    }

    &__options {
        padding: 15px 5px 20px 30px;
        flex-grow: 1;
        display: flex;

        @include medium-size-max {
            padding-left: 15px;
            padding-right: 0;
        }

        @include mobile-menu-max {
            padding: 0;
            flex-wrap: wrap;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        flex-basis: 50%;
        width: 50%;
        max-width: 50%;

        @include mobile-menu-max {
            flex-basis: 100%;
            width: 100%;
            max-width: 100%;
        }
        
        &:first-of-type {
            
            @include mobile-menu-max {
                margin-bottom: 15px;
            }
        }

        &--type {

            .select2-container--default .select2-selection--single .select2-selection__rendered {
    
                &:before {
                    content: '';
                    width: 20px;
                    height: 18px;
                    background-image: url(../img/icons/trailer-icon.svg);
                    background-size: 20px 20px;
                }
            }
        }

        &--store {

            .select2-container--default .select2-selection--single .select2-selection__rendered {

                &:before {
                    content: '\f041';
                    font-size: 22px;
                    display: inline-block;
                    
                    @include fontAwesome;
                }
            }
        }

        &--map {
            .select2-container--default .select2-selection--single {
                background-color: transparent;
                border: none;
                position: relative;
                height: $select-height;
        
                &:before {
                    @include skew(var(--c-blue-main));
                    opacity: 0.1;
                }
            }
        }
    }

    &__item-label {
        color: var(--c-white);
        padding-left: 25px;
        margin-bottom: 5px;
    }

    &__submit {
        font-size: 24px;
        white-space: nowrap;
        margin-right: -0.5px;
        padding-left: 40px;
        padding-right: 40px;

        @include mobile-menu-max {
            width: 100%;
            margin-top: 40px;
            padding-top: 23px;
            padding-bottom: 23px;
            font-size: 20px;
        }

        &:before {

            @include mobile-menu-max {
                border: 2px solid var(--c-white);
                box-shadow: 4px 4px 0 var(--c-white);
            }
        }
    }

    .select2 {
        width: calc(100% - 30px) !important;

        @include mobile-menu-max {
            width: 100%!important;
        }
    }

    .select2-container--default .select2-selection--single {
        background-color: transparent;
        border: none;
        position: relative;
        height: $select-height;

        &:before {
            @include skew(var(--c-white));
            opacity: 0.1;
        }
    }

    .select2-container--default .select2-selection--single .select2-selection__rendered {
        position: relative;
        color: var(--c-white);
        padding-left: 55px;
        font-size: 15px;
        height: 100%;
        display: flex;
        align-items: center;


        &:before {
            position: absolute;
            left: 25px;
            top: 50%;
            transform: translateY(-50%);
        }
    }

    &--category {
        position: static;
        width: auto;
        margin: 0;
        max-width: 570px;

        &:before {
            display: none;
        }

        #{$b} {
            
            &__item-label {
                color: $color-main;
            }

            &__options {
                padding: 0;
                margin: 0 -15px;

                @include small-size-max {
                    margin: 0;
                }
            }

            &__item {

                flex-basis: calc(50% - 30px);
                width: calc(50% - 30px);
                max-width: calc(50% - 30px);
                margin: 0 15px;

                @include small-size-max {
                    flex-basis: 100%;
                    width: 100%;
                    max-width: 100%;
                    margin: 0 0 25px;

                    &:last-of-type {
                        margin-bottom: 0;
                    }
                }

                &--store {

                    .select2-container--default .select2-selection--single .select2-selection__rendered {

                        &:before {
                            content: '\f155';
                            font-size: 20px;
                            left: 28px;
                        }
                    }
                }

                &--type {

                    .select2-container--default .select2-selection--single .select2-selection__rendered {
            
                        &:before {
                            background-image: url(../img/icons/trailer-icon-blue.svg);
                        }
                    }
                }
            }
        }

        .select2 {
            width: 100%!important;
        }

        .select2-container--default .select2-selection--single:before {
            background-color: var(--c-blue-main);
            opacity: 0.07;
        }

        .select2-container--default .select2-selection--single .select2-selection__rendered {
            color: $color-main;
            font-weight: 700;
        }

        span.select2-container--default .select2-selection--single,
        .select2-container--default .select2-selection--single .select2-selection__arrow b {
            border-color: $color-main transparent transparent transparent
        }
    }
}