.tp-warranty {
    article {
        padding-right: 30px;

        @include large-size-max {
            padding-right: 0;
            margin-bottom: 30px;
        }

        p {
            color: var(--c-blue-dark);
            font-family: $f-roboto;
            font-size: 15px;
            font-weight: 400;
            line-height: 26px;
        }
    
        h1 {
            @extend .h2;
            color: var(--c-blue-main);
        }
    
        h2 {
            @extend .h4;
            color: var(--c-blue-dark);
        }
    
        h3 {
            color: var(--c-blue-dark);
            font-family: $f-oswald;
            font-size: 16px;
            font-weight: 500;
            line-height: 20px;
            padding-left: 20px;
            margin-bottom: 10px;
        }
    
        ul {
            padding-left: 20px;
    
            li {
                position: relative;
                padding-left: 10px;
                color: var(--c-blue-dark);
                font-family: $f-roboto;
                font-size: 15px;
                font-weight: 400;
                line-height: 26px;
                margin-bottom: 10px;
    
                &:before {
                    display: block;
                    content: "-";
                    width: 10px;
                    height: 26px;
                    color: var(--c-blue-dark);
                    font-family: $f-roboto;
                    font-size: 15px;
                    font-weight: 400;
                    line-height: 26px;
                    position: absolute;
                    top: 0;
                    left: 0;
                }
            }
        }
    }

    address {
        margin-bottom: 23px;
        color: var(--c-blue-dark);
        font-family: $f-roboto;
        font-size: 15px;
        font-weight: 400;
        line-height: 26px;
        font-style: normal;
    }

    &__manuals {
        background-color: var(--c-white-grey);
        padding: 30px;
        width: calc(100% + 30px);
        margin-left: -30px;
        margin-top: 51px;

        @include large-size-max {
            width: 100%;
            margin-left: 0;
        }

        h3 {
            @extend .h4;
            color: var(--c-blue-main);
        }
    }

    &__manual-item {
        @include clearfix;
        border-top: 1px solid var(--c-gray-dark);
        padding: 20px 0; 

        &:last-child {
            padding-bottom: 0;
        }

        @media screen and (max-width: 500px) {
            display: flex;
            flex-direction: column;
        }
    }

    &__manual-link {
        color: var(--c-blue-main);
        font-size: 14px;
        font-weight: 400;
        line-height: 23px !important;
    }

    &__manual-btn {
        @extend .tp-btn;
        @extend .tp-btn--red;
        padding: 10px 14px;
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
        margin-top: 23px;
        display: inline-block;
        width: fit-content;
    }

    &__manual-img {
        width: auto;
        height: 151px;
        margin-right: 20px;
        display: block;
        float: left;

        @media screen and (max-width: 500px) {
            margin-bottom: 20px;
        }
    }

    &__manual-wrapper {
        display: flex;
        flex-direction: column;
    }

    &__addresses-wrapper {
        address {
            a {
                color: var(--c-blue-dark);

                &:hover {
                    color: var(--c-blue-main);
                }
            }
        }

        @include large-size-max {
            display: flex;
            flex-wrap: wrap;

            address {
                width: 50%;
            }
        }

        @include extra-small-size-max {
            address {
                width: 100%;
            }
        }
    }

    &__manual-list {
        @include large-size-max {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            > div {
                width: 49%;
            }
        }

        @include medium-size-max {
            > div {
                width: 100%;
            }
        }
    }
}