/* ------------------------------------------- */
/*                  FOOTER                     */
/* ------------------------------------------- */

.tp-footer {
    background-color: var(--c-blue-dark);
    padding-top: 85px;
    color: rgba(var(--ca-white), 0.6);

    @include small-size-max {
        padding-top: 50px;
    }

    .tp-logo img {
        height: auto;
    }

    &__text {
        padding: 50px 0;
        font-size: 14px;
        line-height: 23px;
        border-top: 1px solid rgba(var(--ca-white), 0.1);
        margin-top: 50px;

        @include small-size-max {
            margin-top: 0;
            border: none;
        }
    }

    &__copy {
        background-color: var(--c-blue-darken);
        font-size: 14px;
    }

    &__main {
        display: flex;

        @include medium-size-max {
            flex-wrap: wrap;
        }
    }

    &__info {
        max-width: 250px;
        font-size: 15px;

        @include medium-size-max {
            max-width: 100%;
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }

    &__menu {
        display: flex;
        margin-left: auto;

        @include large-size-max {
            margin-left: 40px;
        }

        @include medium-size-max {
            margin-left: 0;
            margin-top: 25px;
            width: 100%;
            justify-content: space-between;
        }

        @include small-size-max {
            flex-wrap: wrap;
            width: 100%;
            justify-content: flex-start;
        }
    }

    &__pay {
        display: flex;
        flex-wrap: wrap;

        li {
            margin-right: 10px;

            &:last-child {
                margin-right: 0;
            }
        }
    }
}